@import "../../common/styles/sass/mixins";
@import "../../common/styles/sass/variables";


.burgerNav {
  width: 100%;
  height: 100%;

  .burgerNavItems {
    //position: fixed;
    @include text($titleFontFamily, 18px, 700, 1.2);
    @include flex(column, center, center, wrap);
    background-color: $bgColor;
    position: absolute;
    right: 0;
    left: 0;
    top: 0;
    //display: none;
    height: 130px;
    padding: 20px;
    transform: translate(0, -140px);
    transition: 0.7s;


    a {
      color: $textColor;
      text-decoration: none;
      margin: 3px;

      &:hover {
        text-decoration: none;
        color: $primaryColor;
        cursor: pointer;
      }
    }

    .active {
      color: $primaryColor;
    }
  }

  .show {
    transform: translate(0, 0);
    //height: 130px;
  }

  @media screen and(min-width: 400px) {
    display: none;
  }

  .burgerBtn {
    width: 60px;
    height: 35px;
    background-color: $primaryColor;
    position: absolute;
    right: 50px;
    top: 22px;
    border-radius: 5px;
  }
}
